import React, {createElement} from 'react';
import {FontProps} from "../font-types";
import {shallowObjectCompare, useAnonymousStyle} from "../../utilities/utilities";
import makeClassString from "classnames";
import {BaseProps} from "../types";
import {processFontProps} from "../type-processors";

export enum TextTag {
    Paragraph = 'p',
    Span = 'span',
    Anchor = 'a',
    Heading1 = 'h1',
    Heading2 = 'h2',
    Heading3 = 'h3',
    Heading4 = 'h4',
    Heading5 = 'h5',
    Heading6 = 'h6',
    Strong = 'strong',
    Emphasized = 'em',
    Bold = 'b',
    Italic = 'i',
    BlockQuote = 'blockquote',
    InlineQuote = 'q',
    Code = 'code',
    Preformatted = 'pre',
    Mark = 'mark',
    Insert = 'ins',
    Delete = 'del',
    Subscript = 'sub',
    Superscript = 'sup',
    Small = 'small'
}

interface RawTextProps {
    tag: TextTag,
    className?: string;
    text: string;
}

function _RawText({tag, className, text}: RawTextProps) {
    return createElement(tag, {className}, text);
}

const RawText = React.memo(_RawText);

type BaseTextProps = FontProps & BaseProps;
export interface TextProps extends BaseTextProps {
    tag: TextTag;
    text: string;
}

function _Text({tag, text, style = {}, classes = [], ...props}: TextProps) {
    const fontProps = processFontProps(props);
    const ref = useAnonymousStyle({...style, ...fontProps});
    const finalClassName = makeClassString(classes, ref.current);
    return <RawText tag={tag} text={text} className={finalClassName}/>
}

export const Text = React.memo(_Text, shallowObjectCompare);
