import React from 'react';
import './BoldHeader.scss';
import {BaseHeaderProps, Header, HeaderTag} from "../../../Foundations/Header";

export function BoldHeader({children, style = {}, classes = [], ...headerProps}: BaseHeaderProps) {
    classes.push('header--bold');
    return <Header level={HeaderTag.Heading1} style={style} classes={classes} {...headerProps}>
        {children}
    </Header>;
}
