import React, {useEffect} from 'react';
import {PositionBlock, PositionTypes} from "../PositionBlock";
import {ActionProps, ComponentProps} from "../types";
import {Flexer, FlexMainAxis, FlexSecondaryAxis} from "../Flexer";
import {targetOnly} from "../../utilities/utilities";
import {Surface} from "../Surface";

type ModalBaseProps = ComponentProps & ActionProps;

export interface ModalProps extends ModalBaseProps {
    background?: string;
    open: boolean;
}

export function Modal({children, style = {}, classes = [], open, ...props}: ModalProps) {
    const {background, onClick = () => ({})} = props;
    useEffect(() => {
        const body = document.getElementsByTagName('body')[0];
        if (open) body.style.overflow = 'hidden';
        return () => {body.style.overflow = 'initial'};
    });
    const visibility = open ? 'initial' : 'hidden';
    return <PositionBlock style={{width: '100%', zIndex: 9999, visibility}} top={0} left={0} position={PositionTypes.Fixed}>
            <Flexer style={{...style}} classes={classes} main={FlexMainAxis.Center}
                    secondary={FlexSecondaryAxis.Center}>
                <Surface onClick={targetOnly(onClick)} width='100%' height='100vh' background={background}>
                    {children}
                </Surface>
            </Flexer>
        </PositionBlock>;
}
