import React, {useEffect, useState} from 'react';
import {ColorProbability, RotatingGradientController} from './RotatingGradientController';
import {SurfaceProps} from "../types";
import {Surface} from "../Surface";

export interface GradientBaseProps extends SurfaceProps {
    colors: ColorProbability[];
}

interface GradientProps extends GradientBaseProps {
    rotation?: number;
}

export function Gradient({rotation = 0, colors, ...props}: GradientProps) {
    const [controller, setController] = useState<RotatingGradientController>(new RotatingGradientController(colors));

    useEffect(() => {
        setController(new RotatingGradientController(colors));
    }, [colors])

    const background = controller.grad(rotation, 0);
    return <Surface {...props} background={background}/>
}
