import React from 'react';
import {FlexerProps} from "./flexer-types";
import {PropsCloner} from "../PropsCloner";

function processFlexerProps(props: FlexerProps): {[key: string]: any} {
    const out: {[key: string]: any} = {display: 'flex'};
    if (props.direction) out['flexDirection'] = props.direction;
    if (props.main) out['justifyContent'] = props.main;
    if (props.secondary) out['alignItems'] = props.secondary;
    if (props.wrap) out['flexWrap'] = props.wrap;
    return out;
}

export function Flexer({children, style, classes = [], ...props}: FlexerProps) {
    const len = React.Children.count(children);
    if (len !== 1) throw Error('Flexer must be passed one child');
    const flexStyles = processFlexerProps(props);
    return PropsCloner({children, props: {style: {...style, ...flexStyles}, classes}});
}
