import React from 'react';
import {FlexDirection, Flexer, FlexSecondaryAxis} from "../../Foundations/Flexer";
import {Surface} from "../../Foundations/Surface";
import {PrimaryHeader} from "../../Components/Headers/PrimaryHeader";
import {Paragraph} from "../../Foundations/Paragraph";
import {HoverBox} from "../../Components/HoverBox";
import {useBreakpoint} from "../../utilities/utilities";
import {
    BreakpointExtraLarge,
    BreakpointLarge,
    BreakpointMedium,
    BreakpointSmall
} from "../../utilities/media_query_manager";

export function CaseStudies() {
    const bp = useBreakpoint();
    let size = '20vw';
    let num = 2;
    if (bp === BreakpointSmall) {
        size = '50vw';
        num = 2;
    } else if (bp === BreakpointMedium) {
        size = '33.3333vw';
        num = 3;
    } else if (bp === BreakpointLarge) {
        size = '25vw';
        num = 4;
    } else if (bp === BreakpointExtraLarge) {
        size = '20vw';
        num = 5;
    }

    const studies = [
        {
            title: 'COVID RESPONSE',
            text: 'AGENCY: FLORIDA DIVISION OF EMERGENCY MANAGEMENT (FDEM), STATE EMERGENCY RESPONSE TEAM (SERT)',
            size,
            src: 'https://acdisaster.com/img/case-studies/april-19-2020/virus-blue-bg.jpg'
        },
        {
            title: 'GRANT MANAGEMENT',
            text: 'AGENCY: Boulder County',
            size,
            src: 'https://acdisaster.com/img/case-studies/case-before.jpg'
        },
        {
            title: 'GRANT MANAGEMENT',
            text: 'AGENCY: STATE OF NORTH DAKOTA OFFICE OF MANAGEMENT BUDGET',
            size,
            src: 'https://acdisaster.com/img/case-studies/ndCase.jpg'
        },
        {
            title: 'COVID-19 RESPONSE (DR-4486)',
            text: 'AGENCY: FLORIDA DIVISION OF EMERGENCY MANAGEMENT (FDEM), STATE EMERGENCY RESPONSE TEAM (SERT)',
            size,
            src: 'https://acdisaster.com/img/case-studies/april-19-2020/virus-with-blue-bg.jpg'
        },
        {
            title: 'OREGON WINTER STORMS & FLOODING RESPONSE',
            text: 'AGENCY: OREGON OFFICE OF EMERGENCY MANAGEMENT (OR OEM)',
            size,
            src: 'https://acdisaster.com/img/case-studies/april-19-2020/road-snow.jpg'
        }
    ]


    return <Surface>
        <Flexer direction={FlexDirection.Column} secondary={FlexSecondaryAxis.Center} style={{marginTop: '4.8rem'}}>
            <Surface>
                <PrimaryHeader>
                    Case Studies
                </PrimaryHeader>
                <Paragraph classes={['font__voice--primary']} style={{marginTop: '2.4rem', marginBottom: '4.8rem'}}>
                    Check out the gallery below to learn more about the communities we have served!
                </Paragraph>
            </Surface>
        </Flexer>
        <Flexer>
            <Surface>
                {Array(num).fill(0).map((e, i) => <HoverBox key={i} {...studies[i]}/>)}
            </Surface>
        </Flexer>
    </Surface>;
}
