import React from 'react';
import {ComponentProps} from "../types";
import {ensureNotPx} from "../../utilities/utilities";
import {PropsCloner} from "../PropsCloner";

export interface ShadowProps extends ComponentProps {
    hoffset?: number | string;
    voffset?: number | string;
    blur?: number | string;
    spread?: number | string;
    color?: string;
}

function processShadowProps(props: ShadowProps): ShadowProps {
    const {hoffset = 0, voffset = 0, blur = 0, spread = 0, color = 'rgba(0, 0, 0, .8)'} = props;
    return {
        hoffset: ensureNotPx(hoffset),
        voffset: ensureNotPx(voffset),
        blur: ensureNotPx(blur),
        spread: ensureNotPx(spread),
        color
    };
}

export function Shadow({children, style = {}, classes = [], ...props}: ShadowProps) {
    const len = React.Children.count(children);
    if (len !== 1) throw Error('Shadow must be passed one child');
    const {hoffset, voffset, blur, spread, color} = processShadowProps(props);
    const boxShadow = `${hoffset} ${voffset} ${blur} ${spread} ${color}`;
    const finalProps = {
        style: {...style, boxShadow},
        classes
    }
    return PropsCloner({children, props: finalProps});
}
