import React from 'react';
import {ComponentProps} from "../types";
import {ensureNotPx} from "../../utilities/utilities";
import {PropsCloner} from "../PropsCloner";

export enum BorderStyle {
    None = 'none',
    Hidden = 'hidden',
    Dotted = 'dotted',
    Dashed = 'dashed',
    Solid = 'solid',
    Double = 'double',
    Groove = 'groove',
    Ridge = 'ridge',
    Inset = 'inset',
    Outset = 'outset',
    Initial = 'initial',
    Inherit = 'inherit',
}

export interface BorderProps extends ComponentProps {
    borderStyle?: BorderStyle;
    borderWidth?: number | string;
    borderColor?: string;
    borderRadius?: number | string;
}

function processBorderProps(props: BorderProps): BorderProps {
    const out: BorderProps = {};
    if (props.borderStyle !== undefined) out.borderStyle = props.borderStyle;
    if (props.borderWidth !== undefined) out.borderWidth = ensureNotPx(props.borderWidth);
    if (props.borderColor !== undefined) out.borderColor = props.borderColor;
    if (props.borderRadius !== undefined) out.borderRadius = ensureNotPx(props.borderRadius);
    return props;
}

export function Border({children, style = {}, classes = [], ...props}: BorderProps) {
    const len = React.Children.count(children);
    if (len !== 1) throw Error('Border must be passed one child');
    const borderProps = processBorderProps(props);
    return PropsCloner({children, props: {style: {...style, ...borderProps}, classes}});
}
