import React, {useEffect, useRef, useState} from 'react';
import {ColorProbability} from '../../Foundations/Gradient/RotatingGradientController';
import {PositionBlock, PositionTypes} from '../../Foundations/PositionBlock';
import './ParallaxScreen.scss';
import {Surface} from "../../Foundations/Surface";
import {RotatingGradient} from "../../Foundations/Gradient";
import {Shadow} from "../../Foundations/Shadow";
import {Border} from "../../Foundations/Border";
import {ensureNotPx, useAnonymousStyle} from "../../utilities/utilities";
import {Transform} from "../../Foundations/Transform";
import {Testimonial, TestimonialName, TestimonialText} from "../../Components/Testimonial";
import {ServicesExpertise} from "../ServicesExpertise/ServicesExpertise";
import {CaseStudies} from "../CaseStudies/CaseStudies";
import {AboutUs} from "../AboutUs";
import {Image} from "../../Foundations/Image";
import {Team} from "../Team/Team";
import {Footer} from "../Footer";

const colors1: ColorProbability[] = [
    {probability: 0, color: '#0076ff'},
    {probability: 25, color: '#009cff'},
    {probability: 50, color: '#0076ff'},
    {probability: 75, color: '#009cff'},
    {probability: 100, color: '#00a0ff'}
];

const colors2 = [
    {probability: 0, color: '#6d00ff'},
    {probability: 16.667, color: '#cc00ff'},
    {probability: 2 * 16.667, color: '#b000ff'},
    {probability: 3 * 16.667, color: '#6d00ff'},
    {probability: 4 * 16.667, color: '#d600ff'},
    {probability: 5 * 16.667, color: '#b00aff'},
    {probability: 6 * 16.667, color: '#b600ff'}
];

function calcScrollPct(height: number, scrollAmt: number) {
    if (scrollAmt < 0) return 0;
    if (scrollAmt > height) return height;
    return scrollAmt / height;
}

const lerp = (x: number, y: number, a: number) => x * (1 - a) + y * a;
const clamp = (a: number, min = 0, max = 1) => Math.min(max, Math.max(min, a));
const genRange = (a: number, b: number, c?: number) => (x: number) => clamp(lerp(a, b, x), a, c || b);

export function ParallaxScreen() {
    const [height, setHeight] = useState<number>(0);
    const [scrollPct, setScrollPct] = useState<number>(0);
    const screenRef = useRef<HTMLDivElement>(null);

    const range1 = genRange(-5, 250, 250);
    const range2 = genRange(-90, 90);
    const range3 = genRange(5, 300, 50);

    useEffect(() => {
        const handler = () => screenRef.current && setHeight(screenRef.current.offsetHeight);
        handler();
        window.addEventListener('resize', handler);
        return () => window.removeEventListener('resize', handler);
    }, []);

    useEffect(() => {
        const handler = () => setScrollPct(calcScrollPct(height, window.scrollY));
        window.addEventListener('scroll', handler);
        return () => window.removeEventListener('scroll', handler);
    });

    // prevent flashes
    // @ts-ignore
    const ref = useAnonymousStyle({visibility: 'initial !important'})
    return <div style={{visibility: 'hidden', overflow: 'hidden', position: 'relative'}} className={ref.current}>
        <Surface height='100vh' width='100%'>
            <div ref={screenRef} className='parallax_screen'>
                <Image src='./spin_logo.png' alt='SPIN Logo'
                       style={{width: '30vw', position: 'absolute', top: '40vh', left: 'calc(50vw - 30vw/2)'}}/>
                <PositionBlock top={.75 * range1(scrollPct) + 'vh'} left='5vw'
                               position={PositionTypes.Relative}>
                    <Shadow blur={2 * range3(scrollPct)}>
                        <Border borderRadius='50%'>
                            <Transform rotate='30deg'>
                                <RotatingGradient colors={colors1}
                                                  width={range3(scrollPct) + 'vw'}
                                                  height={range3(scrollPct) + 'vw'}
                                                  stepSize={25}/>
                            </Transform>
                        </Border>
                    </Shadow>
                </PositionBlock>
                <Transform rotate={5 * range1(scrollPct) + 'deg'} transformOrigin='-25px -25px'>
                    <PositionBlock top={75 - .1 * range1(scrollPct) + 'vh'} left={'85vw'}
                                   position={PositionTypes.Absolute}>
                        <Shadow blur={4}>
                            <Border borderRadius='50%'>
                                <Transform rotate='-45deg'>
                                    <RotatingGradient colors={colors2}
                                                      width={5 * Math.sin(16 * scrollPct) + 15 + 'vw'}
                                                      height={'15vw'}
                                                      stepSize={2}/>
                                </Transform>
                            </Border>
                        </Shadow>
                    </PositionBlock>
                </Transform>
            </div>
        </Surface>
        <PositionBlock top={-range2(scrollPct) + 'vh'} left={0} right={0} style={{zIndex: 2}}
                       position={PositionTypes.Absolute}>
            <Shadow blur={64} spread={2} color='rgba(0, 0, 0, 0.60)'>
                <Border borderRadius='8px'>
                    <Surface height='initial' background='white' width={'96%'}
                             style={{margin: '0 auto', maxWidth: ensureNotPx(1440)}}>
                        <AboutUs/>
                    </Surface>
                </Border>
            </Shadow>
        </PositionBlock>
        <Testimonial background='#22a77c' style={{zIndex: 1, position: 'relative'}}>
            <TestimonialText>
                AC Disaster Consulting was the only firm that understood exactly, including the magnitude, of what
                Pasco County was looking for. Alyssa and her team worked relentlessly to recover every cent that was
                eligible for reimbursement. Alyssa’s team never missed a deadline and in nearly all of the projects
                were weeks or months ahead of schedule. I can affirm that her firm was the first, by weeks, to
                submit reimbursements that have netted over $5M in received obligated funds.
            </TestimonialText>
            <TestimonialName name='Kevin Guthrie' title='Former Pasco County Administrator'/>
        </Testimonial>
        <ServicesExpertise/>
        <Testimonial background='#d64d57' style={{zIndex: 1, position: 'relative'}}>
            <TestimonialText>
                AC Disaster Consulting are the rare practitioner/policy experts who leverage their vast networks of
                emergency management professionals to continually improve the services they provide based on those
                experiences and their analysis of how federal policy is interpreted and implemented in real time. Their
                knowledge of federal recovery programs, policies, and practices are incomparable.
            </TestimonialText>
            <TestimonialName name='Andrew J. Phelps'
                             title='State Emergency Management Director, Oregon Military Department'/>
        </Testimonial>
        <CaseStudies/>
        <Testimonial background='#bc8458' style={{zIndex: 1, position: 'relative'}}>
            <TestimonialText>
                AC Disaster Consulting made sure every eligible dollar was identified and put forth for consideration
                for reimbursement from prior disasters. The team also demonstrated a strong talent for dealing with
                often confusing and conflicting language from FEMA and FDEM as well as advocated to ensure that Pasco
                County was treated fairly.
            </TestimonialText>
            <TestimonialName name='Patrick Ballinger'
                             title='Grants Coordinator, Pasco County Board of County Commissioners'/>
        </Testimonial>
        <Team/>
        <Testimonial background='#e9b543' style={{zIndex: 1, position: 'relative'}}>
            <TestimonialText>
                We as a City are privileged to be in the very capable and experienced hands of AC Disaster Consulting.
                With their help our recovery has moved quicker than anyone ever expected. Alyssa has broad connections
                within Federal and State agencies and is well-respected in her field.
            </TestimonialText>
            <TestimonialName name='Tanya Castro'
                             title='City Administrator, City of Mexico Beach'/>
        </Testimonial>
        <Footer/>
    </div>
}
