import {ensureNotPxEm} from "./utilities";

export type BreakpointDims = Map<Symbol, string>;

export const BreakpointNone = Symbol('none');
export class MediaQueryManager extends EventTarget {
    private watchers: Array<MediaQueryList> = [];
    private symbols: Array<Symbol> = [];
    private _active: Symbol = BreakpointNone;


    constructor(breaks: Array<number | string>) {
        super();
        const handler = () => {
            for(let i = 0; i < this.watchers.length; i++) {
                if(this.watchers[i].matches) {
                    this._active = this.symbols[i];
                    break;
                }
            }
            this.changed();
        }
        const finalBreaks = [...breaks, 99999];
        for(const pt of finalBreaks) {
            const dim = ensureNotPxEm(pt);
            const query = `(max-width: ${dim})`;
            const watcher = window.matchMedia(query);
            watcher.addEventListener('change', handler);
            this.watchers.push(watcher);
            const symb = Symbol(query);
            this.symbols.push(symb);
        }
        handler();
    }

    changed() {
        this.dispatchEvent(new Event('change'));
    }

    get breaks() {
        return this.symbols
    }

    get active() {
        return this._active;
    }
}
