import React from 'react';
import '../../styles/font.scss';
import {FontProps} from "../font-types";
import {ComponentProps} from "../types";
import {Text, TextTag} from "../Text";
import {shallowObjectCompare} from "../../utilities/utilities";

export type ParagraphProps = FontProps & ComponentProps;

export function _Paragraph({children, style = {}, classes = [], ...props}: ParagraphProps) {
    if(typeof children !== 'string') throw Error('Can only pass a string child to Paragraph');
    return <Text tag={TextTag.Paragraph} text={children} style={style} classes={classes} {...props} />;
}

export const Paragraph = React.memo(_Paragraph, shallowObjectCompare);
