import React, {PropsWithChildren} from 'react';
import {PresentationProps} from "../../Foundations/types";
import {Surface} from "../../Foundations/Surface";
import {Border} from "../../Foundations/Border";
import '../../styles/font.scss';
import {Paragraph} from "../../Foundations/Paragraph";
import {FontStyle, TextAlign} from "../../Foundations/font-types";
import {useBreakpoint} from "../../utilities/utilities";
import {
    BreakpointExtraLarge,
    BreakpointLarge,
    BreakpointMedium,
    BreakpointSmall
} from "../../utilities/media_query_manager";

export function TestimonialText({children}: PropsWithChildren<{}>) {
    const text = `"${children}"`;
    return <Paragraph fontStyle={FontStyle.Italic} textAlign={TextAlign.Center} classes={['font__voice--primary']}
                      style={{padding: '3.6rem 3.6rem .6rem 3.6rem'}}>
        {text}
    </Paragraph>
}

export interface TestimonialNameProps {
    name: string;
    title: string;
}

export function TestimonialName({name, title}: TestimonialNameProps) {
    return <Surface style={{padding: '.6rem 3.6rem 1.2rem 3.6rem'}}>
        <Paragraph classes={['font__voice--primary-leadin']}>{name}</Paragraph>
        <Paragraph classes={['font__voice--primary']}>
            {title}
        </Paragraph>
    </Surface>

}

export interface TestimonialProps extends PresentationProps {

}

export function Testimonial({children, style = {}, classes = [], ...props}: TestimonialProps) {
    const {background} = props;
    let width = '65%';
    const bp = useBreakpoint();
    switch (bp) {
        case BreakpointSmall:
            width = '90%';
            break;
        case BreakpointMedium:
            width = '90%';
            break;
        case BreakpointLarge:
            width = '65%';
            break;
        case BreakpointExtraLarge:
            width = '65%';
            break;
        default:
            break;
    }
    return <Surface background={background} width='100%' style={{...style, overflow: 'auto'}}>
        <Border borderRadius={8}>
            <Surface background='white' width={width} style={{margin: '4.8rem auto 4.8rem auto'}}>
                {children}
            </Surface>
        </Border>
    </Surface>;
}
