import React from 'react';
import {SurfaceProps} from "../types";
import {processActionProps, processSurfaceProps} from "../type-processors";
import makeClassString from "classnames";
import {useAnonymousStyle} from "../../utilities/utilities";

export function _Surface({children, style = {}, classes = [], ...props}: SurfaceProps) {
    const surf = processSurfaceProps(props);
    const ref = useAnonymousStyle({...surf, ...style});
    const className = makeClassString(classes, ref.current);
    const actions = processActionProps(props);
    return <div onClickCapture={actions.onClick} className={className}>
        {children}
    </div>;
}

export const Surface = React.memo(_Surface);
