import React, {createElement} from 'react';
import {SurfaceProps} from '../types';
import makeClassString from 'classnames';
import {processSurfaceProps} from '../type-processors';
import {shallowObjectCompare, useAnonymousStyle} from '../../utilities/utilities';

interface BaseImageProps {
    src: string;
    alt: string;
}

interface RawImageProps extends BaseImageProps {
    className?: string;
}

export type ImageProps = SurfaceProps & BaseImageProps;

function _RawImage({src, alt, className}: RawImageProps) {
    return createElement('img', {className, src, alt});
}

const RawImage = React.memo(_RawImage);

function processImageProps(props: ImageProps): ImageProps {
    const {src = '', alt = ''} = props;
    return {...processSurfaceProps(props), src, alt};
}

function _Image({style = {}, classes = [], ...props}: ImageProps) {
    const {src, alt, ...surf} = processImageProps(props);
    const ref = useAnonymousStyle({...surf, ...style});
    const className = makeClassString(classes, ref.current);
    return <RawImage src={src} alt={alt} className={className}/>;
}

export const Image = React.memo(_Image, shallowObjectCompare);
