import React from 'react';
import {Surface} from "../../Foundations/Surface";
import {Hover} from "../../Foundations/Hover";
import {PositionBlock, PositionTypes} from "../../Foundations/PositionBlock";
import {BoldHeader} from "../Headers/BoldHeader";
import {Paragraph} from "../../Foundations/Paragraph";
import {FlexDirection, Flexer, FlexMainAxis, FlexSecondaryAxis} from "../../Foundations/Flexer";
import {TextAlign} from "../../Foundations/font-types";

export interface HoverBoxProps {
    size: number | string;
    src: string;
    title: string;
    text: string;
}

export function HoverBox({size, src, title, text}: HoverBoxProps) {
    return <Hover hoverStyles={{backgroundSize: '120%'}} subStyles={{
        ' > div': {visibility: 'initial'}
    }}>
        <Surface width={size} height={size}
                 style={{backgroundSize: '100%', backgroundPosition: 'center', transition: 'all .1s ease'}}
                 background={`url(${src})`}>
            <Flexer direction={FlexDirection.Column} main={FlexMainAxis.Center} secondary={FlexSecondaryAxis.Center}>
                <PositionBlock position={PositionTypes.Relative} style={{alignSelf: 'stretch', height: '100%', background: 'rgba(0, 0, 0, .5)', visibility: 'hidden'}}>
                    <BoldHeader textAlign={TextAlign.Center} style={{color: 'white !important', fontSize: '2.4rem !important'}}>{title}</BoldHeader>
                    <Paragraph textAlign={TextAlign.Center} classes={['font__voice--primary']} style={{color: 'white !important', marginTop: '2.4rem'}}>{text}</Paragraph>
                </PositionBlock>
            </Flexer>
        </Surface>;
    </Hover>
}
