import {ActionProps, DimensionProps, PositionProps, PresentationProps, SurfaceProps} from "./types";
import {ensureNotPx} from "../utilities/utilities";
import {FontProps} from "./font-types";

export function processDimensionProps(props: DimensionProps): DimensionProps {
    const out: DimensionProps = {};
    if (props.height !== undefined) out.height = ensureNotPx(props.height);
    if (props.width !== undefined) out.width = ensureNotPx(props.width);
    return out;
}

export function processPresentationProps(props: PresentationProps): PresentationProps {
    const out: PresentationProps = {};
    if (props.background !== undefined) out.background = props.background;
    return out;
}

export function processSurfaceProps(props: SurfaceProps): SurfaceProps {
    return {...processDimensionProps(props), ...processPresentationProps(props)};
}

export function processPositionProps(props: PositionProps): PositionProps {
    const out: PositionProps = {};
    if (props.top !== undefined) out.top = props.top;
    if (props.left !== undefined) out.left = props.left;
    if (props.right !== undefined) out.right = props.right;
    if (props.bottom !== undefined) out.bottom = props.bottom;
    return out;
}

export function processActionProps(props: ActionProps): ActionProps {
    const {
        onClick = () => {
        }
    } = props;
    return {onClick};
}

export function processFontProps(props: FontProps): FontProps {
    const out: FontProps = {};
    if (props.fontVariant) out.fontVariant = props.fontVariant;
    if (props.fontStyle) out.fontStyle = props.fontStyle;
    if (props.fontWeight) out.fontWeight = props.fontWeight;
    if (props.fontSize) out.fontSize = ensureNotPx(props.fontSize);
    if (props.lineHeight) out.lineHeight = ensureNotPx(props.lineHeight);
    if (props.fontFamily) out.fontFamily = props.fontFamily;
    if (props.textAlign) out.textAlign = props.textAlign;
    return props;
}
