import {CSSProperties} from 'react';
import {ComponentProps} from "../types";
import {ensureNotPx} from "../../utilities/utilities";
import {PropsCloner} from "../PropsCloner";

export interface Coordinates2D {
    x: number | string;
    y: number | string;
}

export interface TransformProps extends ComponentProps {
    translate?: Coordinates2D;
    scale?: Coordinates2D;
    skew?: Coordinates2D;
    rotate?: string;
    transformOrigin?: string;
    inlineBlock?: boolean;
}

function processCoordinates2D(coord: Coordinates2D) {
    const {x, y} = coord;
    return {x: ensureNotPx(x), y: ensureNotPx(y)};
}

function processTransformProps(props: TransformProps): TransformProps {
    const out: TransformProps = {};
    if (props.translate) out.translate = processCoordinates2D(props.translate);
    if (props.scale) out.scale = props.scale;
    if (props.skew) out.skew = props.skew;
    if (props.rotate) out.rotate = props.rotate;
    if (props.transformOrigin) out.transformOrigin = props.transformOrigin;
    if (props.inlineBlock) out.inlineBlock = props.inlineBlock;
    return out;
}

function coordToString(coord: Coordinates2D): string {
    const {x, y} = coord;
    return `(${x}, ${y})`;
}

function makeTransformString(props: TransformProps) {
    const subStyles = [];
    if (props.translate) subStyles.push('translate' + coordToString(props.translate));
    if (props.scale) subStyles.push('scale' + coordToString(props.scale));
    if (props.skew) subStyles.push('skew' + coordToString(props.skew));
    if (props.rotate) subStyles.push('rotate(' + props.rotate + ')');
    return subStyles.length === 0 ? 'initial' : subStyles.join(' ');
}

export function Transform({children, style = {}, classes = [], ...props}: TransformProps) {
    const {transformOrigin, inlineBlock, ...transformProps} = processTransformProps(props);
    const transform = makeTransformString(transformProps);
    const anonStyles: CSSProperties = {transform, transformOrigin};
    if(inlineBlock) anonStyles.display = 'inline-block';
    return PropsCloner({children, props: {style: {...style, ...anonStyles}, classes}});
}
