import React from 'react';
import {ActionProps, PositionProps} from "../types";
import {processActionProps, processPositionProps} from "../type-processors";
import {Surface} from "../Surface";

type PositionDimensionProps = PositionProps & ActionProps;

export enum PositionTypes {
    Absolute = 'absolute',
    Fixed = 'fixed',
    Initial = 'initial',
    Relative = 'relative',
    Static = 'static',
    Sticky = 'sticky'
}

export interface PositionBlockProps extends PositionDimensionProps {
    position: PositionTypes;
}

export function PositionBlock({children, style = {}, classes = [], ...props}: PositionBlockProps) {
    const {position} = props;

    const pos = processPositionProps(props);

    const actions = processActionProps(props);
    return <Surface style={{...style, ...pos, position}}
                    classes={classes}
                    {...actions}>
        {children}
    </Surface>;
}
