import React from 'react';
import './PrimaryHeader.scss';
import {BaseHeaderProps, Header, HeaderTag} from "../../../Foundations/Header";
import {FontWeight} from "../../../Foundations/font-types";

export function PrimaryHeader({children, style = {}, classes = [], ...headerProps}: BaseHeaderProps) {
    classes.push('header--primary');
    return <Header level={HeaderTag.Heading1} fontWeight={FontWeight.Bolder} style={style} classes={classes} {...headerProps}>
        {children}
    </Header>;
}
