export class IdManager {
    ids: Set<string> = new Set<string>();

    static _makeId(length: number) {
        let result = '';
        const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz';
        const len = chars.length;
        for (let i = 0; i < length; i++) {
            result += chars.charAt(Math.floor(Math.random() * len));
        }
        return result;
    }

    constructor(private numFails = 50) {};

    removeId(id: string) {
        this.ids.delete(id);
    }

    addId(id: string) {
        this.ids.add(id);
    }

    genId(len: number) {
        let id = IdManager._makeId(len);
        let i = 0;
        while(this.ids.has(id)) {
            id = IdManager._makeId(len);
            if(i++ === this.numFails) {
                throw Error('IdManager has reached maximum number of retries, this length is potentially fully.');
            }
        }
        return id;
    }
}
