import React from 'react';
import {ComponentProps} from "../../Foundations/types";
import {Surface} from "../../Foundations/Surface";
import {FlexDirection, Flexer, FlexSecondaryAxis} from "../../Foundations/Flexer";
import {Border, BorderStyle} from "../../Foundations/Border";
import {Image} from "../../Foundations/Image";
import {Paragraph} from "../../Foundations/Paragraph";
import {Shadow} from "../../Foundations/Shadow";
import {PositionBlock, PositionTypes} from "../../Foundations/PositionBlock";
import {Hover} from "../../Foundations/Hover";

export interface EmployeeCardProps extends ComponentProps {
    src: string;
    name: string;
    title: string;
    bio: string;
    ln?: string;
}

export function EmployeeCard({src, name, title, bio, ln}: EmployeeCardProps) {
    return <Flexer direction={FlexDirection.Column}>
        <Border borderRadius={2} borderWidth={8} borderStyle={BorderStyle.Solid} borderColor='white'>
            <Shadow blur={16}>
                <Hover hoverStyles={{}} subStyles={{' > div:nth-of-type(2)': {top: '-15rem'}}}>
                    <Surface width={330} height={414} style={{overflow: 'hidden'}}>
                        <Image width={330} src={src} alt='CEO'/>
                        <Flexer direction={FlexDirection.Column} secondary={FlexSecondaryAxis.Center}>
                            <Surface style={{marginTop: '1.2rem'}}>
                                <Paragraph classes={['font__voice--primary-leadin']}>
                                    {name}
                                </Paragraph>
                                <Paragraph classes={['font__voice--primary']} style={{marginTop: '1.2rem'}}>
                                    {title}
                                </Paragraph>
                            </Surface>
                        </Flexer>
                        <PositionBlock position={PositionTypes.Relative} style={{top: '2.0rem', transition: 'all .15s ease-out'}}>
                            <Surface width={330} height={180} background='rgba(0, 0, 0, 0.8)'
                                     style={{borderBottomRightRadius: '.2rem', borderBottomLeftRadius: '.2rem'}}>
                                <Paragraph classes={['font__voice--primary']}
                                           style={{color: 'white !important', fontSize: '1.8rem !important', padding: '1.8rem'}}>
                                    {bio}
                                </Paragraph>
                            </Surface>
                        </PositionBlock>
                    </Surface>
                </Hover>
            </Shadow>
        </Border>
    </Flexer>
}
