import React from 'react';
import {FontProps} from "../font-types";
import {ComponentProps} from "../types";
import {Text, TextTag} from "../Text";
import {shallowObjectCompare} from "../../utilities/utilities";

export enum HeaderTag {
    Heading1 = TextTag.Heading1,
    Heading2 = TextTag.Heading2,
    Heading3 = TextTag.Heading3,
    Heading4 = TextTag.Heading4,
    Heading5 = TextTag.Heading5,
    Heading6 = TextTag.Heading6,
}

export type BaseHeaderProps = FontProps & ComponentProps;
export interface HeaderProps extends BaseHeaderProps {
    level: HeaderTag;
}

function _Header({children, style = {}, classes = [], level,  ...props}: HeaderProps) {
    if(typeof children !== 'string') throw Error('Can only pass a string child to Header');
    return <Text tag={(level as unknown) as TextTag} text={children} style={style} classes={classes} {...props} />
}

export const Header = React.memo(_Header, shallowObjectCompare);
