import {CSSProperties} from 'react';
import {ComponentProps} from "../types";
import {useAnonymousStyle, useStyles} from "../../utilities/utilities";
import {PropsCloner} from "../PropsCloner";

export interface HoverProps extends ComponentProps {
    hoverStyles: CSSProperties;
    subStyles?: {[key: string]: CSSProperties};
}

export function Hover({children, style = {}, classes = [], hoverStyles = {}, subStyles = {}}: HoverProps) {
    const ref = useAnonymousStyle(hoverStyles, ':hover');
    useStyles(subStyles, `.${ref.current}:hover`);
    const finalClasses = [...classes];
    if(ref.current) finalClasses.push(ref.current);
    return PropsCloner({children, props: {style, classes: finalClasses}});
}
